
import { defineComponent } from 'vue';
import { ref } from "vue";
import { getAuth, createUserWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import router from '@/router';
import { useRouter } from 'vue-router';
import MenuBar from '@/components/menuBar.vue';
import FooterBar from '@/components/FooterBar.vue';
import { insertUser } from '@/services/api';

export default defineComponent({
    name: 'signup-view',
    components: {

        MenuBar,
        FooterBar
    },


    data() {
        const email = ref('');
        const password = ref('');
        const router = useRouter();

        return {
            email,
            password,
            router,
        }
    },

    methods: {
        register() {
            console.log(this.email);
            createUserWithEmailAndPassword(getAuth(), this.email.trim(), this.password)

                .then((data) => {
                    alert("Successfully registered!")
                    insertUser(
                        {
                            displayName: data.user.displayName,
                            email: data.user.email,
                            photoURL: "https://source.unsplash.com/random/151x151/",
                            sexo: "",
                            uid: data.user.uid,
                        }
                    )
                    router.push(`/Painel/${data.user.uid}`);
                }).catch((error) => {
                    // console.log(error.code);
                    alert(error.message);
                })
        },
        signInWithGoogle() {
            const provider = new GoogleAuthProvider();
            signInWithPopup(getAuth(), provider).then((result) => {

                insertUser(
                    {
                        displayName: result.user.displayName,
                        email: result.user.email,
                        photoURL: "https://source.unsplash.com/random/151x151/",
                        sexo: "",
                        uid: result.user.uid,
                    }
                )
                router.push(`/Painel/${result.user.uid}`);
            }).catch((error) => {
                alert(error.message)
            });
        }

    }


}
)

